import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {filterAriaAttributes} from '@servicenow/library-enhanced-utils';
import styles from './_now-text-link.scss';
import {t} from 'sn-translate';
import focusBehavior from '@servicenow/behavior-focus';
import {configAriaSchema} from '@servicenow/library-schemas';

const view = (state, {dispatch}) => {
	const {
		label = '',
		href = '',
		variant,
		underlined,
		opensWindow,
		configAria,
		download
	} = state.properties;
	return (
		<a
			on-click={() => {
				dispatch(() => {
					return {
						type: 'NOW_TEXT_LINK#CLICKED',
						payload: {}
					};
				});
			}}
			href={href}
			class={{
				'now-text-link': true,
				['-' + variant]: variant,
				'show-underline': underlined
			}}
			target={opensWindow ? '_blank' : undefined}
			rel={opensWindow ? 'noopener' : undefined}
			attr-download={download}
			{...filterAriaAttributes(configAria, 'link')}
			aria-describedby={opensWindow ? 'linkLabel' : undefined}>
			{label}
			{opensWindow ? (
				<span id="linkLabel" hidden>
					{t('Link opens in new window')}
				</span>
			) : null}
		</a>
	);
};

/**
 * A text link is a stylized element of text that is linked to another resource.
 * You can distinguish a text link from regular body text by its secondary color.
 *
 * Text links are different from buttons. Buttons are reserved for actions.
 * See the usage guidance for the Button component for more information.
 *
 * ```jsx
 * <now-text-link label="Link text" href="http://example.com" variant="secondary">
 * </now-text-link>
 * ```
 *
 * @seismicElement now-text-link
 * @summary A styled link displayed as a text label.
 * @uib.label Text link
 * @uib.icon text-link
 * @uib.description A text link is a stylized element of text that is linked to another resource
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, variant, href, underlined, opensWindow, download, configAria
 * @uib.actions NOW_TEXT_LINK#CLICKED
 */
createEnhancedElement('now-text-link', {
	properties: {
		/**
		 * Text displayed as a link.
		 * @type {string}
		 * @uib.description Text displayed as a link
		 * @uib.defaultValue Link
		 * @uib.translatable true
		 */
		label: {required: true, schema: {type: 'string'}},
		/**
		 * Link URL opened when the user clicks link text.
		 * @type {string}
		 * @uib.label Link
		 * @uib.description URL opened when the user clicks link text
		 * @uib.defaultValue https://www.servicenow.com/
		 */
		href: {required: true, schema: {type: 'string'}},
		/**
		 * Sets link colors.
		 * @type {('primary'|'secondary')}
		 * @uib.label Variant
		 * @uib.description Sets link colors
		 */
		variant: {
			default: 'primary',
			schema: {type: 'string', enum: ['primary', 'secondary']}
		},
		/**
		 * When true, underlines the link at all times, not only on hover.
		 * All links show underline on hover.
		 * @type {boolean}
		 * @uib.description Select to underline the link at all times
		 */
		underlined: {default: false, schema: {type: 'boolean'}},
		/**
		 * Opens link in a new window or tab (depending on user's browser
		 * configuration). "Link opens in new window" is announced
		 * to assistive technology users.
		 *
		 * Note: Open links in new browser windows or tabs sparingly 
		 * because it is less accessible for users.
		 * @type {boolean}
		 * @uib.label Open new window
		 * @uib.description Opens link in a new window or tab (depending on user's browser configuration). "Link opens in new window" is announced to assistive technology users.
		 */
		opensWindow: {default: false, schema: {type: 'boolean'}},
		/**
		 * An object whose keys reference a specific HTML element within the
		 * now-text-link. The value of each key is an object that contains an ARIA
		 * property (or properties) and corresponding value/s. These ARIA
		 * properties are set on the specified element.
		 * The 'link' key corresponds to the inner html `<a>` element in
		 * now-text-link. See https://www.w3.org/TR/wai-aria-1.1/#link for
		 * properties and accepted values.
		 * @type {{ 'aria-*': string }}
		 * @uib.label ARIA properties
		 * @uib.defaultValue {"aria-label": "Accessible Label"}
		 * @uib.description Configure ARIA properties
		 */
		configAria: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							link: configAriaSchema
						},
						additionalProperties: false
					},
					configAriaSchema
				]
			}
		},
		/**
		 * When true, the browser downloads the file instead of
		 * navigating to a URL. Provide the value as a string to pre-populate the
		 * filename of the downloaded file.
		 * @type {(string|boolean)}
		 * @uib.label Filename override
		 * @uib.description Set to instruct the browser to download a file instead of navigating to a URL. Provide the value as a string to pre-populate the filename of the downloaded file
		 * @uib.fieldType string
		 */
		download: {schema: {type: ['string', 'boolean']}}
	},
	view,
	styles,
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	dispatches: {
		/**
		 * Dispatched when the link is clicked.
		 * @type {{}}
		 * @uib.description Dispatched when the link is clicked
		 */
		'NOW_TEXT_LINK#CLICKED': {
			schema: {
				type: 'object'
			}
		}
	}
});
